.App {
  text-align: center;
  background-color: rgba(237, 244, 250, 0.61);
  min-height: 650px;
  font-family: 'Baloo 2', cursive;
  font-size: 17px;
}

.AppWrapper{
  background-image: url("./PhotosBackground/gallery_img-03.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  background-attachment: fixed;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: rgba(8, 58, 196, 0.651);
  font-family: 'Rowdies', cursive;
  height: 70px;
  padding: 20px;
  color: white;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.397);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.397);
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.message{
  display: none;
  font-size: 17px;
  margin-bottom: 15px;
}

.ContentWrapper{
  display:inline-block;
  padding: 30px 80px;
  width: 650px;
  border-radius: 7px;
  text-align: left;
  background-color: rgb(255, 250, 250);
  margin-top: 30px;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
}

.UserWrapper{
  text-align: center;
}
.UserContent{
  display: -ms-flexbox;
  display: flex;
  margin-bottom: -15px;
}

.UserContent img{
  border-radius: 40px;
  height: 80px;
}

.UserBreifDetail{
  margin-left: 30px;
}

.UserBreifDetail h5{
  margin-bottom: 2px;
  font-size: 25px;
}

.UserBreifDetail a{
  color: rgb(15, 158, 194);
}

.Seen{
  font-size: 14px;
  color: rgb(5, 155, 72);
  margin-bottom: 4px;
}

.LastActive{
  font-size: 19px;
  margin-right: 4px;
}

.activityCheck{
  font-size: 19px;
  margin-right: 4px;
}

.collapse p{
  font-size: 17px;
}

.Date{
  font-size: 16px;
  margin-bottom: 13px;
}

.btn-danger{
  width:100px;
}

footer{
  color: white;
  padding: 15px 25px;
  background-color: rgb(0, 0, 0);
  background-size: cover;
  width: 100%;
  display: inline-block;
  font-family: 'Baloo 2', cursive;
  font-size: 17px;
}

.Footer{
  text-align: center;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media only screen and (max-width: 426px) {
  .ContentWrapper{
    width: 99%;
    padding: 20px 10px;
  }
  .UserBreifDetail{
    margin-left: 10px;
  }
  .LastActive{
    font-size: 17px;
  }
  .collapse{
    margin-top:-20px;
    margin-left: 10px;
  }
  .activityCheck{
    font-size: 17px;
  }
  .Date{
    font-size: 13px;
    margin-bottom: 13px;
  }
  .message{
    font-size: 15px;
  }
  .collapse p{
    font-size: 16px;
  }
    
}



